import React, { useState, useEffect } from 'react';
import {
  Box, Container,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Paper,
  CardActionArea, Card, CardContent, Typography, Grid,
  Dialog, DialogTitle, DialogContent, DialogActions, Tooltip, IconButton,
} from '@mui/material';
import {
  Close as CloseIcon,
} from '@mui/icons-material';
import {
  amber, blueGrey, brown, grey,
} from '@mui/material/colors';



function Ranking(props) {
  const [ranking, setRanking] = useState([]);
  const [characterDetail, setCharacterDetail] = useState(0);
  const [itemDetail, setItemDetail] = useState(0);



  const saveRanking = (data) => {
    props.setSeverity(data.result ? 'success' : 'warning');
    props.setMessage(data.message);

    // 
    if (!data.result) {
      return;
    }

    props.saveCookieJwtToken(data.jwtToken);
    setRanking(data.ranking);
  }

  useEffect(() => {
    (async () => {
      props.setIsRunning(true);

      props.setSeverity('info');
      props.setMessage('...');

      // 
      const options = {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: `action=ranking&jwtToken=${props.jwtToken}`
      }

      await fetch(props.url, options)
        .then(res => res.json())
        .then(data => {
          saveRanking(data);
        }).catch((error) => {
          props.setSeverity('error');
          props.setMessage(error.message);
        })

      props.setIsRunning(false);
    })()
  }, []);



  return (
    <Box>
      <Container>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell
                  component='th'
                  scope='row'
                  align='center'>
                  
                </TableCell>
                <TableCell align='center'>
                  <Grid container direction='column' spacing={1}>
                    <Grid item>
                      <Typography></Typography>
                    </Grid>
                    <Grid item>
                      <Typography></Typography>
                    </Grid>
                    <Grid item>
                      <Typography></Typography>
                    </Grid>
                  </Grid>
                </TableCell>
                <TableCell align='center'>
                  
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {ranking.map((rank, index) => (
                <TableRow
                  key={index + 1}
                  sx={{
                    bgcolor: index === 0 ? amber[200]
                      : (index === 1 ? blueGrey[200]
                        : (index === 2 ? brown[200]
                          : grey[200]))
                  }}>
                  <TableCell
                    component='th'
                    scope='row'
                    align='center'>
                    {index + 1}
                  </TableCell>
                  <TableCell align='center'>
                    <Grid container direction='column' spacing={1}>
                      <Grid item>
                        <Typography variant='h6'>{rank.userName}</Typography>
                      </Grid>
                      <Grid item>
                        <Typography>{rank.score}</Typography>
                      </Grid>
                      <Grid item>
                        <Typography>{rank.totalDistance / 1000 + 'km'}</Typography>
                      </Grid>
                    </Grid>
                  </TableCell>

                  <TableCell align='center'>
                    <Grid container spacing={1} wrap='nowrap'>
                      {rank.characterList.map((character) => (
                        <Grid item key={character.id} xs={12} sm={6} md={4}>
                          <CardActionArea
                            key={String(index + 1) + String(character.id)}
                            onClick={() => {
                              setCharacterDetail(character.number);
                              setItemDetail(character.item);
                            }}>
                            <Card sx={{
                              position: 'relative',
                              paddingTop: '100%',
                              overflow: 'hidden',
                              backgroundColor: props.convertTypeColor(props.charactersList[character.number].rare)
                            }}>
                              <CardContent>
                                <img
                                  src={`${process.env.PUBLIC_URL}/images/characters/${props.charactersList[character.number].fileName}.png`}
                                  alt={props.charactersList[character.number].name}
                                  style={{
                                    objectFit: 'contain',
                                    position: 'absolute',
                                    top: 0,
                                    left: 0,
                                    width: '100%',
                                    height: '100%',
                                    maxWidth: '100%',
                                    maxHeight: '100%',
                                  }}
                                />
                                <img
                                  src={`${process.env.PUBLIC_URL}/images/items/${props.itemsList[character.item].fileName}.png`}
                                  alt={props.itemsList[character.item].name}
                                  style={{
                                    objectFit: 'contain',
                                    position: 'absolute',
                                    bottom: 0,
                                    right: 0,
                                    width: '25%',
                                    height: '25%',
                                    maxWidth: '100%',
                                    maxHeight: '100%',
                                  }}
                                />
                              </CardContent>
                            </Card>
                            <Box
                              sx={{
                                px: 0.5,
                                position: 'absolute',
                                bottom: '-5%',
                                height: 30,
                                zIndex: 1,
                              }}
                            >
                              <Typography variant='subtitle2' textAlign='left'>
                                {character.level}
                              </Typography>
                            </Box>
                          </CardActionArea>
                        </Grid>
                      ))}
                    </Grid>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Container>

      <Dialog
        open={characterDetail > 0 && itemDetail > 0}
        onClose={() => {
          setCharacterDetail(0);
          setItemDetail(0);
        }}
        PaperProps={{
          style: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }
        }}>
        <DialogTitle color={'primary'}></DialogTitle>
        <DialogContent>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow
                  sx={{ backgroundColor: 'primary.light' }}>
                  <TableCell sx={{ backgroundColor: 'primary.main' }} />
                  <TableCell align='right' sx={{ color: 'white' }}>
                    
                  </TableCell>
                  <TableCell align='right' sx={{ color: 'white' }}>
                    
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow key='number'>
                  <TableCell
                    component='th'
                    scope='row'
                    sx={{ color: 'white', backgroundColor: 'primary.main' }}
                  >
                    No.
                  </TableCell>
                  <TableCell align='right'>{characterDetail}</TableCell>
                  <TableCell align='right'>{itemDetail}</TableCell>
                </TableRow>
                {props.detailRow.map((row) => (
                  <TableRow key={row.id}>
                    <TableCell
                      component='th'
                      scope='row'
                      sx={{ color: 'white', backgroundColor: 'primary.main' }}>
                      {row.name}
                    </TableCell>
                    <TableCell align='right'>{props.charactersList[characterDetail]?.[row.id]}</TableCell>
                    <TableCell align='right'>{props.itemsList[itemDetail]?.[row.id]}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </DialogContent>

        <DialogActions>
          <Tooltip title=''>
            <IconButton
              onClick={() => {
                setCharacterDetail(0);
                setItemDetail(0);
              }}
            >
              <CloseIcon color='inherit' />
            </IconButton>
          </Tooltip>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default Ranking;