import React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box, Typography, Button,
} from '@mui/material';

function Error404() {

  const navigate = useNavigate();

  return (
    <Box
      sx={{
        marginTop: 16,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <Typography></Typography>
      <Button
        variant='outlined'
        onClick={() => navigate('/')}
        sx={{ marginTop: 16 }}
      >
        
      </Button>

    </Box>
  );
}

export default Error404;