import React, { useState } from 'react';
import {
  Box, Container, Grid, Button, Typography,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Paper,
} from '@mui/material';
import {
  ViewList as ViewListIcon,
  Rule as RuleIcon,
  Balance as BalanceIcon,
  Person as PersonIcon,
} from '@mui/icons-material';
import {
  indigo, cyan, lightGreen, yellow, red, grey,
} from '@mui/material/colors';

import CategoryTable from './CategoryTable';



function Settings(props) {
  const [showTypesList, setShowTypesList] = useState('categoryTable');



  const types = props.typesList.map((item) => item.type);

  // 
  function effectiveColor(effective) {
    switch (effective) {
      case 0.2:
        return indigo[100];
      case 0.5:
        return cyan[100];
      case 1:
        return lightGreen[100];
      case 2:
        return yellow[100];
      case 4:
        return red[100];
      default:
        return grey[100];
    }
  }


  const settingsCategories = [
    {
      id: 'categoryTable',
      title: '',
      icon: <ViewListIcon />
    }, {
      id: 'typesEffective',
      title: '',
      icon: <BalanceIcon />
    }, {
      id: 'rule',
      title: '',
      icon: <RuleIcon />
    }, {
      id: 'user',
      title: '',
      icon: <PersonIcon />
    }
  ];



  return (
    <Box>
      <Container>
        <Grid container spacing={2} sx={{ mt: 1, textAlign: 'center' }}>
          {settingsCategories.map((category) => (
            <Grid item key={category.id} xs={3} textAlign='center'>
              <Button
                variant={category.id === showTypesList ? 'contained' : 'outlined'}
                onClick={() => setShowTypesList(category.id)}
                sx={{
                  width: '100%'
                }}
              >
                {category.icon}
                <Typography
                  sx={{
                    px: 2,
                    display: { xs: 'none', sm: 'flex' },
                  }}>
                  {category.title}
                </Typography>
              </Button>
            </Grid>
          ))}
        </Grid>

        {(showTypesList === 'categoryTable') && (
          <CategoryTable
            charactersList={props.charactersList}
            itemsList={props.itemsList}
            detailRow={props.detailRow}
            convertTypeColor={props.convertTypeColor}
          />
        )}

        {(showTypesList === 'typesEffective') && (
          <Box>
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell align='center' sx={{ p: { xs: 0, sm: 2} }}>
                      <Typography sx={{ textAlign: 'right', color: 'secondary.main' }}></Typography>
                      <Typography sx={{ textAlign: 'center' }}>\</Typography>
                      <Typography sx={{ textAlign: 'left', color: 'primary.main' }}></Typography>
                    </TableCell>
                    {types.map((type) => (
                      <TableCell key={type} align='center' sx={{ color: 'secondary.main', p: { xs: 0, sm: 2} }}>{type}</TableCell>
                    ))}
                  </TableRow>
                </TableHead>
                <TableBody>
                  {props.typesList.map((effective) => (
                    <TableRow key={effective.type}>
                      <TableCell align='center' sx={{ color: 'primary.main', p: { xs: 0, sm: 2} }}>{effective.type}</TableCell>
                      {types.map((t) => (
                        <TableCell key={t} align='right' sx={{ bgcolor: effectiveColor(effective[t]), p: { xs: 0, sm: 2} }}>{effective[t]}</TableCell>
                      ))}
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Box>
        )}

        {(showTypesList === 'rule') && (
          <Box>
            <TableContainer component={Paper} sx={{ mt: 1 }}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.addCharacterLevelInterval}m
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.addGachaCountInterval}m
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>

            <Box sx={{ mt: 1, borderTop: 1 }} />

            <TableContainer component={Paper} sx={{ mt: 1 }}>
              <Table>
                <TableBody>
                  <TableRow sx={{ backgroundColor: props.convertTypeColor(1) }}>
                    <TableCell component='th' scope='row'>
                      1
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rare1}%
                    </TableCell>
                  </TableRow>

                  <TableRow sx={{ backgroundColor: props.convertTypeColor(2) }}>
                    <TableCell component='th' scope='row'>
                      2
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rare2}%
                    </TableCell>
                  </TableRow>

                  <TableRow sx={{ backgroundColor: props.convertTypeColor(3) }}>
                    <TableCell component='th' scope='row'>
                      3
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rare3}%
                    </TableCell>
                  </TableRow>

                  <TableRow sx={{ backgroundColor: props.convertTypeColor(4) }}>
                    <TableCell component='th' scope='row'>
                      4
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rare4}%
                    </TableCell>
                  </TableRow>

                  <TableRow sx={{ backgroundColor: props.convertTypeColor(5) }}>
                    <TableCell component='th' scope='row'>
                      5
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rare5}%
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>

            <Box sx={{ mt: 1, borderTop: 1 }} />

            <TableContainer component={Paper} sx={{ mt: 1 }}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rateCharacters}%
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.rateItems}%
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>

            <Box sx={{ mt: 1, borderTop: 1 }} />

            <TableContainer component={Paper} sx={{ mt: 1 }}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.sumPartyCharacter}
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.initialCharacterLevel}
                    </TableCell>
                  </TableRow>

                  <TableRow>
                    <TableCell component='th' scope='row'>
                      
                    </TableCell>
                    <TableCell>
                      {props.settingsList.initialGachaCount}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>

            <Box sx={{ mt: 1, borderTop: 1 }} />

          </Box>
        )}

        {(showTypesList === 'user') && (
          <Box>
            <TableContainer component={Paper} sx={{ mt: 1 }}>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell>
                      
                    </TableCell>
                    <TableCell>
                      {props.userName}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
          </Box>
        )}

      </Container>
    </Box>
  );
}

export default Settings;