import React from 'react';
import { MapContainer, TileLayer, LayersControl, Marker, Popup, CircleMarker, Tooltip, Polyline } from 'react-leaflet';



function Map(props) {


	return (
		<MapContainer center={[props.latitude, props.longitude]} zoom={12} style={{ height: '50vh', width: '100%' }}>
			<TileLayer
				url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'
				attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
			/>
			<LayersControl position='topright'>
				<LayersControl.Overlay checked name=''>
					<Marker position={[props.latitude, props.longitude]}>
						<Popup></Popup>
					</Marker>
				</LayersControl.Overlay>
				<LayersControl.Overlay checked name=''>
					<CircleMarker
						center={[props.lastLatitude, props.lastLongitude]}
						radius={18}>
						<Tooltip></Tooltip>
					</CircleMarker>
				</LayersControl.Overlay>
				<LayersControl.Overlay checked name=''>
					<Polyline positions={[[props.latitude, props.longitude], [props.lastLatitude, props.lastLongitude]]} />
				</LayersControl.Overlay>
			</LayersControl>
		</MapContainer>
	);
}

export default Map;
