import React, { useState } from 'react';
import {
  Box,
  IconButton, Button, Tooltip,
  Dialog, DialogTitle, DialogContent, DialogActions,
  Container, Paper,
  CardActionArea, Card, CardContent,
  TableContainer, Table, TableHead, TableRow, TableBody, TableCell,
} from '@mui/material';
import {
  Close as CloseIcon,
} from '@mui/icons-material';


function Gacha(props) {
  const [gachaDetail, setGachaDetail] = useState(0);



  const saveGachaData = (data) => {
    props.setSeverity(data.result ? 'success' : 'warning');
    props.setMessage(data.message);

    // 
    if (!data.result) {
      return;
    }

    props.saveCookieJwtToken(data.jwtToken);
    props.setUserInfo(data.userInfo);

    props.setGachaCategory(data.userInfo.gachaCategory);
    props.setGachaNumber(data.userInfo.gachaNumber);
    props.setGachaSelect(true);
  }

  const turnGacha = async () => {
    props.setIsRunning(true);
    
    props.setGachaSelect(false);
    props.setGachaNumber(0);
    props.setGachaCategory('');
    props.setAddDistance(0);
    props.setAddCharacterLevel(0);
    props.setAddGachaCount(0);

    props.setSeverity('info');
    props.setMessage('...');

    // 
    const options = {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: `action=gacha&jwtToken=${props.jwtToken}`
    }

    await fetch(props.url, options)
      .then(res => res.json())
      .then(data => {
        saveGachaData(data);
      }).catch((error) => {
        props.setSeverity('error');
        props.setMessage(error.message);
      })

    props.setIsRunning(false);
  }



  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}>
      <Container sx={{ px: 16, py: 2 }} maxWidth='xs'>
        <CardActionArea
          disabled={props.gachaSelect ? false : true}
          onClick={() => setGachaDetail(props.gachaNumber)}
        >
          <Card sx={{
            position: 'relative',
            paddingTop: '100%',
            overflow: 'hidden',
            visibility: props.gachaSelect ? 'visible' : 'hidden',
            backgroundColor: props.convertTypeColor(props.charactersList[props.gachaNumber]?.rare)
          }}>
            <CardContent>
              <img
                src={props.gachaCategory === 'characters' ? (
                  `${process.env.PUBLIC_URL}/images/characters/${props.charactersList[props.gachaNumber]?.fileName}.png`
                ) : (
                  `${process.env.PUBLIC_URL}/images/items/${props.itemsList[props.gachaNumber]?.fileName}.png`
                )}
                alt={props.gachaCategory === 'characters' ? (
                  props.charactersList[props.gachaNumber]?.name
                ) : (
                  props.itemsList[props.gachaNumber]?.name
                )}
                style={{
                  objectFit: 'contain',
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  width: '100%',
                  height: '100%',
                  maxWidth: '100%',
                  maxHeight: '100%',
                }}
              />
            </CardContent>
          </Card>
        </CardActionArea>
      </Container>

      <Dialog
        open={gachaDetail > 0}
        onClose={() => {
          setGachaDetail(0);
        }}
        PaperProps={{
          style: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }
        }}>
        <DialogTitle color={'secondary'}></DialogTitle>
        <DialogContent>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow
                  sx={{ backgroundColor: 'secondary.light' }}>
                  <TableCell sx={{ backgroundColor: 'secondary.main' }} />
                  <TableCell align='right' sx={{ color: 'white' }}>
                    {props.gachaCategory === 'characters' ? '' : ''}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow key='number'>
                  <TableCell
                    component='th'
                    scope='row'
                    sx={{ color: 'white', backgroundColor: 'secondary.main' }}
                  >
                    No.
                  </TableCell>
                  <TableCell align='right'>{gachaDetail}</TableCell>
                </TableRow>
                {props.detailRow.map((row) => (
                  <TableRow key={row.id}>
                    <TableCell
                      component='th'
                      scope='row'
                      sx={{ color: 'white', backgroundColor: 'secondary.main' }}>
                      {row.name}
                    </TableCell>
                    <TableCell align='right'>
                      {props.gachaCategory === 'characters' ? props.charactersList[gachaDetail]?.[row.id] : props.itemsList[gachaDetail]?.[row.id]}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </DialogContent>

        <DialogActions>
          <Tooltip title=''>
            <IconButton
              onClick={() => {
                setGachaDetail(0);
              }}
            >
              <CloseIcon color='inherit' />
            </IconButton>
          </Tooltip>
        </DialogActions>
      </Dialog>

      <Button
        variant='contained'
        disabled={props.isRunning || !(props.gachaCount > 0 ? true : false)}
        onClick={turnGacha}
        sx={{
          width: '80%'
        }}
      >
        
      </Button>
    </Box>
  );
}

export default Gacha;