import React, { useState, useEffect } from 'react';
import {
  Box, Container, Grid, Button, Typography, Card,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Paper,
} from '@mui/material';
import {
  EmojiEmotions as EmojiEmotionsIcon,
  Construction as ConstructionIcon,
} from '@mui/icons-material';


function CategoryTable(props) {
  const [tableCategory, setTableCategory] = useState('characters');
  const [categoryList, setCategoryList] = useState(props.charactersList);

  useEffect(() => {
    switch (tableCategory) {
      case 'characters':
        setCategoryList(props.charactersList);
        break;
      case 'items':
        setCategoryList(props.itemsList);
        break;
      default:
        setCategoryList(props.charactersList);
    }
  }, [tableCategory]);



  const categories = [
    {
      id: 'characters',
      title: '',
      icon: <EmojiEmotionsIcon />
    }, {
      id: 'items',
      title: '',
      icon: <ConstructionIcon />
    }
  ];



  const categoryRow = Object.keys(categoryList).map(id => (
    <TableRow key={id}>
      <TableCell component='th' scope='row' align='center'>
        {id}
      </TableCell>
      <TableCell align='center'>
        <Card
          sx={{
            py: 1,
            backgroundColor: props.convertTypeColor(categoryList[id].rare)
          }}>
          <img
            src={`${process.env.PUBLIC_URL}/images/${tableCategory}/${categoryList[id].fileName}.png`}
            alt={categoryList[id].name}
            style={{ maxWidth: 50, maxHeight: 50 }}
          />
        </Card>
        <Typography>
          {categoryList[id].name}
          </Typography>
      </TableCell>

      <TableCell align='center'>
        <Grid container spacing={2} sx={{ mt: 1, textAlign: 'center' }}>
          <Grid item xs={6} textAlign='center'>
            {categoryList[id].mainType}
          </Grid>
          <Grid item xs={6} textAlign='center'>
            {categoryList[id].subType}
          </Grid>
          <Grid item xs={3} textAlign='center'>
            {categoryList[id].hp}
          </Grid>
          <Grid item xs={3} textAlign='center'>
            {categoryList[id].attack}
          </Grid>
          <Grid item xs={3} textAlign='center'>
            {categoryList[id].defense}
          </Grid>
          <Grid item xs={3} textAlign='center'>
            {categoryList[id].speed}
          </Grid>
        </Grid>
      </TableCell>
    </TableRow>
  ))




  return (
    <Box>
      <Container>
        <Grid container spacing={2} sx={{ mt: 1, textAlign: 'center' }}>
          {categories.map((category) => (
            <Grid item key={category.id} xs={6} textAlign='center'>
              <Button
                variant={category.id === tableCategory ? 'contained' : 'outlined'}
                onClick={() => setTableCategory(category.id)}
                sx={{
                  width: '100%'
                }}
              >
                {category.icon}
                <Typography
                  sx={{
                    px: 2,
                    display: { xs: 'none', sm: 'flex' },
                  }}>
                  {category.title}
                </Typography>
              </Button>
            </Grid>
          ))}
        </Grid>
      </Container>

      <TableContainer component={Paper} sx={{ mt: 2 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell align='center'>No.</TableCell>
              <TableCell align='center'></TableCell>
              <TableCell align='center'>
                <Grid container spacing={2} sx={{ textAlign: 'center' }}>
                  <Grid item xs={6} textAlign='center'>
                    
                  </Grid>
                  <Grid item xs={6} textAlign='center'>
                    
                  </Grid>
                  <Grid item xs={3} textAlign='center'>
                    
                  </Grid>
                  <Grid item xs={3} textAlign='center'>
                    
                  </Grid>
                  <Grid item xs={3} textAlign='center'>
                    
                  </Grid>
                  <Grid item xs={3} textAlign='center'>
                    
                  </Grid>
                </Grid>
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {categoryRow}
          </TableBody>
        </Table>
      </TableContainer>

    </Box>
  );
}

export default CategoryTable;