import React, { useState, useEffect } from 'react';
import { useCookies } from 'react-cookie';
import {
	Box,
	AppBar, Toolbar, IconButton, Typography, Button, Tooltip,
	Alert, AlertTitle, Snackbar,
	Backdrop, CircularProgress,
} from '@mui/material';
import {
	Room as RoomIcon,
	MyLocation as MyLocationIcon,
	Map as MapIcon,
	Attractions as AttractionsIcon,
	FormatListNumbered as FormatListNumberedIcon,
	SportsEsports as SportsEsportsIcon,
	Home as HomeIcon,
} from '@mui/icons-material';
import {
	amber, deepPurple, deepOrange, teal, blueGrey, grey,
} from '@mui/material/colors';

import UserInfo from './children/UserInfo';
import Map from './children/Map';
import Gacha from './children/Gacha';
import Ranking from './children/Ranking';
import Settings from './children/Settings';



function Journey() {
	const [isRunning, setIsRunning] = useState(false);
	const [canPosition, setCanPosition] = useState(true);
	const [canMap, setCanMap] = useState(true);
	const [latitude, setLatitude] = useState(null);
	const [longitude, setLongitude] = useState(null);
	const [lastLatitude, setLastLatitude] = useState(null);
	const [lastLongitude, setLastLongitude] = useState(null);
	const [addCharacterLevel, setAddCharacterLevel] = useState(0);
	const [addGachaCount, setAddGachaCount] = useState(0);
	const [userInfo, setUserInfo] = useState({
		userName: '',
		latitude: null,
		longitude: null,
		latestDistance: 0,
		todayDistance: 0,
		totalDistance: 0,
		gachaCount: 0,
		gachaNumber: 0,
		gachaCategory: '',
		characterList: []
	});
	const [charactersList, setCharactersList] = useState([]);
	const [itemsList, setItemsList] = useState([]);
	const [typesList, setTypesList] = useState([]);
	const [settingsList, setSettingsList] = useState({});

	const [addDistance, setAddDistance] = useState(0);
	const [severity, setSeverity] = useState('info');
	const [message, setMessage] = useState('Maitake Journey');

	const [gachaNumber, setGachaNumber] = useState(0);
	const [gachaCategory, setGachaCategory] = useState('');
	const [gachaSelect, setGachaSelect] = useState(false);

	const [showDisplay, setShowDisplay] = useState('map');

	const [cookies, setCookie] = useCookies(['JWT_TOKEN']);

	const url = 'https://script.google.com/macros/s/AKfycbyFeKa-8h0An7TCIvVxZaeMsAKwGSB68WF0bOfHHtxejIutUX6-wb7f_Otw1F4jVkyh/exec';



	// JWTCookie
	const saveCookieJwtToken = (jwtToken) => {
		try {
			// .
			const payloadBase64 = jwtToken.split('.')[1];
			// Base64
			const payload = JSON.parse(atob(payloadBase64));
			// UnixJavaScript30
			const cookieExpiration = new Date(payload.exp * 1000 + 3600 * 24 * 30);
			setCookie('JWT_TOKEN', jwtToken, { path: '/', expires: cookieExpiration, secure: true, sameSite: 'strict' });
		} catch (error) {
			setSeverity('error');
			setMessage(error.message);
		}
	}

	// 
	const saveMapData = (data) => {
		setSeverity(data.result ? 'success' : 'warning');
		setMessage(data.message);

		setCharactersList(data.charactersList);
		setItemsList(data.itemsList);
		setTypesList(data.typesList);
		setSettingsList(data.settingsList);

		// 
		if (!data.result) {
			return;
		}

		saveCookieJwtToken(data.jwtToken);
		setUserInfo(data.userInfo);

		// 
		if (!data.initialize) {
			setLastLatitude(data.lastLatitude);
			setLastLongitude(data.lastLongitude);
			setAddCharacterLevel(data.addCharacterLevel);
			setAddGachaCount(data.addGachaCount);
			setAddDistance(data.userInfo.latestDistance);
		}

		// 
		if (!data.isUpdate) {
			setAddCharacterLevel(0);
			setAddGachaCount(0);
			setAddDistance(0);
		}
	}

	// 
	useEffect(() => {
		(async () => {
			setIsRunning(true);

			// 
			if (!canPosition) {
				setIsRunning(false);
				return;
			}
			setCanPosition(false);

			setSeverity('info');
			setMessage('...');

			try {
				const position = await new Promise((resolve, reject) => {
					navigator.geolocation.getCurrentPosition(resolve, reject);
				});
				// 
				setLatitude(position.coords.latitude);
				setLongitude(position.coords.longitude);
				setLastLatitude(position.coords.latitude);
				setLastLongitude(position.coords.longitude);
			} catch (error) {
				setSeverity('error');
				setMessage(error.message);
			}
			setIsRunning(false);
		})()
	}, []);

	// latitudelongitude
	useEffect(() => {
		(async () => {
			setIsRunning(true);

			// 
			if (!canMap || latitude === null || longitude === null) {
				setIsRunning(false);
				return;
			}
			setCanMap(false);

			setSeverity('info');
			setMessage('...');

			// 
			const options = {
				method: 'POST',
				headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
				body: `action=map&jwtToken=${cookies.JWT_TOKEN}&latitude=${latitude}&longitude=${longitude}`
			}

			await fetch(url, options)
				.then(res => res.json())
				.then(data => {
					if (data.jwtToken === 'undefined') {
						window.location.href = 'https://MaitakeTeikoku.github.io/MaitakeAuth';
					}
					saveMapData(data);
				}).catch((error) => {
					setSeverity('error');
					setMessage(error.message);
				})
			setIsRunning(false);
		})()
	}, [latitude, longitude]);



	const headerActions = [
		{
			id: 'map',
			title: '',
			tooltip: '',
			icon: <MapIcon />
		}, {
			id: 'gacha',
			title: '',
			tooltip: '',
			icon: <AttractionsIcon />
		}, {
			id: 'ranking',
			title: '',
			tooltip: '',
			icon: <FormatListNumberedIcon />
		}, {
			id: 'settings',
			title: '',
			tooltip: '',
			icon: <SportsEsportsIcon />
		}
	];

	const convertTypeColor = (rare) => {
		switch (rare) {
			case 5:
				return amber[100];
			case 4:
				return deepPurple[100];
			case 3:
				return deepOrange[100];
			case 2:
				return teal[100];
			case 1:
				return blueGrey[100];
			default:
				return grey[100];
		}
	}

	const detailRow = [
		{ id: 'name', name: '' },
		{ id: 'rare', name: '' },
		{ id: 'mainType', name: '' },
		{ id: 'subType', name: '' },
		{ id: 'hp', name: '' },
		{ id: 'attack', name: '' },
		{ id: 'defense', name: '' },
		{ id: 'speed', name: '' }
	];



	// 
	if (latitude === null || longitude === null || lastLatitude === null || lastLongitude === null) {
		return (
			<Box
				sx={{
					marginTop: 24,
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'center',
				}}
			>
				<RoomIcon
					color='primary'
					sx={{ width: 64, height: 64 }}
				/>
				<Typography
					component='h1'
					variant='h5'
					sx={{
						marginTop: 8,
					}}
					color='primary'>
					{message === '' ? '...' : message}
				</Typography>

			</Box>
		);
	}

	return (
		<Box>
			<AppBar position='relative'>
				<Toolbar disableGutters>
					<Tooltip title='Maitake Journey'>
						<IconButton
							onClick={() => window.location.reload()}
							sx={{ pl: 2, mr: 0 }}
						>
							<MyLocationIcon sx={{ color: 'white' }} />
							<Typography
								color='white'
								sx={{
									pl: 2,
									mr: 2,
									display: { xs: 'none', md: 'flex' },
									fontFamily: 'monospace',
									fontWeight: 700,
									letterSpacing: '.3rem',
									textDecoration: 'none',
								}}>
								MAITAKE JOURNEY
							</Typography>
						</IconButton>
					</Tooltip>

					<Box sx={{ flexGrow: 1, display: 'flex' }}>
						{headerActions.map((actions) => (
							<Tooltip key={actions.tooltip} title={actions.tooltip}>
								<span>
									<Button
										disabled={isRunning || showDisplay === actions.id}
										onClick={() => setShowDisplay(actions.id)}
										sx={{ my: 2, color: 'white' }}
									>
										{actions.icon}
										<Typography
											sx={{
												px: 2,
												display: { xs: 'none', md: 'flex' },
											}}>
											{actions.title}
										</Typography>
									</Button>
								</span>
							</Tooltip>
						))}
					</Box>

					<Tooltip title='Maitake Auth'>
						<IconButton
							disabled={isRunning}
							href='https://MaitakeTeikoku.github.io/MaitakeAuth'
							sx={{ p: 0, mr: 2 }}
						>
							<HomeIcon sx={{ color: 'white' }} />
						</IconButton>
					</Tooltip>

				</Toolbar>
			</AppBar>

			<Snackbar
				open={!!message}
				autoHideDuration={5000}
				onClose={() => setMessage('')}
			>
				<Alert severity={severity} onClose={() => setMessage('')}>
					<AlertTitle>{severity.toUpperCase()}</AlertTitle>
					{message}
				</Alert>
			</Snackbar>

			{(showDisplay === 'map' || showDisplay === 'gacha') && (
				<UserInfo
					url={url}
					jwtToken={cookies.JWT_TOKEN}
					isRunning={isRunning}
					userInfo={userInfo}
					addDistance={addDistance}
					addCharacterLevel={addCharacterLevel}
					addGachaCount={addGachaCount}
					charactersList={charactersList}
					itemsList={itemsList}
					showDisplay={showDisplay}
					gachaSelect={gachaSelect}
					detailRow={detailRow}
					setIsRunning={setIsRunning}
					setUserInfo={setUserInfo}
					setSeverity={setSeverity}
					setMessage={setMessage}
					setGachaNumber={setGachaNumber}
					setGachaCategory={setGachaCategory}
					setGachaSelect={setGachaSelect}
					saveCookieJwtToken={saveCookieJwtToken}
					convertTypeColor={convertTypeColor}
				/>
			)}

			{(showDisplay === 'map') && (
				<Map
					latitude={latitude}
					longitude={longitude}
					lastLatitude={lastLatitude}
					lastLongitude={lastLongitude}
				/>
			)}

			{(showDisplay === 'gacha') && (
				<Gacha
					url={url}
					jwtToken={cookies.JWT_TOKEN}
					isRunning={isRunning}
					gachaCount={userInfo.gachaCount}
					gachaNumber={gachaNumber}
					gachaCategory={gachaCategory}
					gachaSelect={gachaSelect}
					charactersList={charactersList}
					itemsList={itemsList}
					detailRow={detailRow}
					setIsRunning={setIsRunning}
					setUserInfo={setUserInfo}
					setSeverity={setSeverity}
					setMessage={setMessage}
					setGachaNumber={setGachaNumber}
					setGachaCategory={setGachaCategory}
					setGachaSelect={setGachaSelect}
					setAddDistance={setAddDistance}
					setAddCharacterLevel={setAddCharacterLevel}
					setAddGachaCount={setAddGachaCount}
					saveCookieJwtToken={saveCookieJwtToken}
					convertTypeColor={convertTypeColor}
				/>
			)}

			{(showDisplay === 'ranking') && (
				<Ranking
					url={url}
					jwtToken={cookies.JWT_TOKEN}
					isRunning={isRunning}
					charactersList={charactersList}
					itemsList={itemsList}
					detailRow={detailRow}
					saveCookieJwtToken={saveCookieJwtToken}
					setIsRunning={setIsRunning}
					setSeverity={setSeverity}
					setMessage={setMessage}
					convertTypeColor={convertTypeColor}
				/>
			)}

			{(showDisplay === 'settings') && (
				<Settings
					charactersList={charactersList}
					itemsList={itemsList}
					detailRow={detailRow}
					typesList={typesList}
					settingsList={settingsList}
					userName={userInfo.userName}
					convertTypeColor={convertTypeColor}
				/>
			)}

			<Backdrop
				open={isRunning}
				sx={{ color: 'primary.light', zIndex: (theme) => theme.zIndex.drawer + 1 }}
			>
				<CircularProgress color='inherit' />
			</Backdrop>
		</Box>
	);
}

export default Journey;
