import React, { useState } from 'react';
import {
  Box,
  Container, Grid, Badge, Paper, Typography,
  CardActionArea, Card, CardContent, Button,
  Dialog, DialogTitle, DialogContent, DialogActions,
  TableContainer, Table, TableHead, TableRow, TableCell, TableBody,
  Tooltip, IconButton,
} from '@mui/material';
import {
  Today as TodayIcon,
	CalendarMonth as CalendarMonthIcon,
  Attractions as AttractionsIcon,
  Close as CloseIcon,
} from '@mui/icons-material';
import {
	pink, grey,
} from '@mui/material/colors';



function UserInfo(props) {
  const [characterDetail, setCharacterDetail] = useState(0);
	const [itemDetail, setItemDetail] = useState(0);



  // 
	const saveSelectGachaData = (data) => {
		props.setSeverity(data.result ? 'success' : 'warning');
		props.setMessage(data.message);

		// 
		if (!data.result) {
			return;
		}

		props.saveCookieJwtToken(data.jwtToken);
		props.setUserInfo(data.userInfo);
		props.setGachaSelect(false);
		props.setGachaNumber(0);
		props.setGachaCategory('');
	}

	// 
	const selectGacha = async (id) => {
		props.setIsRunning(true);

		props.setSeverity('info');
		props.setMessage('...');

		// 
		const options = {
			method: 'POST',
			headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
			body: `action=selectGacha&jwtToken=${props.jwtToken}&selectId=${id}`
		}

		await fetch(props.url, options)
			.then(res => res.json())
			.then(data => {
				saveSelectGachaData(data);
			}).catch((error) => {
				props.setSeverity('error');
				props.setMessage(error.message);
			})

      props.setIsRunning(false);
	}



  const mapAddResults = [
		{ icon: <TodayIcon />, title: '', sumResult: props.userInfo.todayDistance / 1000 + 'km', addResult: props.addDistance / 1000 },
		{ icon: <CalendarMonthIcon />, title: '', sumResult: props.userInfo.totalDistance / 1000 + 'km', addResult: props.addDistance / 1000 },
		{ icon: <AttractionsIcon />, title: '', sumResult: props.userInfo.gachaCount + '', addResult: props.addGachaCount }
	];



  return (
    <Box>
      <Container>
        <Grid container spacing={2} sx={{ mt: 1, textAlign: 'center' }}>
          {mapAddResults.map((result) => (
            <Grid item key={result.title} xs={4}>
              <Badge
                color='success'
                badgeContent={result.addResult}
                max={999}
                sx={{
                  width: '100%',
                }}
              >
                <Paper
                elevation={0}
                sx={{
                  backgroundColor: grey[100],
                  width: '100%',
                }}>
                  {result.icon}{result.title}
                  <Typography variant='h6' color={pink[500]}>
                    {result.sumResult}
                  </Typography>
                </Paper>
              </Badge>
            </Grid>
          ))}
        </Grid>
      </Container>

      <Container sx={{ py: 2 }} maxWidth='md'>
        <Grid container spacing={1} wrap='nowrap'>
          {props.userInfo.characterList.map((character) => (
            <Grid item key={character.id} xs={12} sm={6} md={4}>
              <CardActionArea
                onClick={() => {
                  setCharacterDetail(character.number);
                  setItemDetail(character.item);
                }}>
                <Card sx={{
                  position: 'relative',
                  paddingTop: '100%',
                  overflow: 'hidden',
                  backgroundColor: props.convertTypeColor(props.charactersList[character.number].rare)
                }}>
                  <CardContent>
                    <img
                      src={`${process.env.PUBLIC_URL}/images/characters/${props.charactersList[character.number].fileName}.png`}
                      alt={props.charactersList[character.number].name}
                      style={{
                        objectFit: 'contain',
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                        maxWidth: '100%',
                        maxHeight: '100%',
                      }}
                    />
                    <img
                      src={`${process.env.PUBLIC_URL}/images/items/${props.itemsList[character.item].fileName}.png`}
                      alt={props.itemsList[character.item].name}
                      style={{
                        objectFit: 'contain',
                        position: 'absolute',
                        bottom: 0,
                        right: 0,
                        width: '25%',
                        height: '25%',
                        maxWidth: '100%',
                        maxHeight: '100%',
                      }}
                    />
                  </CardContent>
                </Card>
                <Badge
                  color='success'
                  badgeContent={props.addCharacterLevel}
                  max={9999}
                >
                  <Box
                    sx={{
                      px: 0.5,
                      position: 'absolute',
                      bottom: '-5%',
                      left: '-5%',
                      height: 35,
                      zIndex: 1,
                    }}
                  >
                    <Typography textAlign='left'>
                      {character.level}
                    </Typography>
                  </Box>
                </Badge>
              </CardActionArea>
              <Button
                variant='outlined'
                disabled={props.isRunning}
                onClick={() => selectGacha(character.id)}
                sx={{
                  display: props.showDisplay === 'gacha' ? 'block' : 'none',
                  visibility: props.gachaSelect ? 'visible' : 'hidden',
                  width: '100%',
                  mt: 1,
                }}>
                
              </Button>
            </Grid>
          ))}
        </Grid>
      </Container>

      <Dialog
        open={characterDetail > 0 && itemDetail > 0}
        onClose={() => {
          setCharacterDetail(0);
          setItemDetail(0);
        }}
        PaperProps={{
          style: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }
        }}>
        <DialogTitle color={'primary'}></DialogTitle>
        <DialogContent>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow
                  sx={{ backgroundColor: 'primary.light' }}>
                  <TableCell sx={{ backgroundColor: 'primary.main' }} />
                  <TableCell align='right' sx={{ color: 'white' }}>
                    
                  </TableCell>
                  <TableCell align='right' sx={{ color: 'white' }}>
                    
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                <TableRow key='number'>
                  <TableCell
                    component='th'
                    scope='row'
                    sx={{ color: 'white', backgroundColor: 'primary.main' }}
                  >
                    No.
                  </TableCell>
                  <TableCell align='right'>{characterDetail}</TableCell>
                  <TableCell align='right'>{itemDetail}</TableCell>
                </TableRow>
                {props.detailRow.map((row) => (
                  <TableRow key={row.id}>
                    <TableCell
                      component='th'
                      scope='row'
                      sx={{ color: 'white', backgroundColor: 'primary.main' }}>
                      {row.name}
                    </TableCell>
                    <TableCell align='right'>{props.charactersList[characterDetail]?.[row.id]}</TableCell>
                    <TableCell align='right'>{props.itemsList[itemDetail]?.[row.id]}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </DialogContent>

        <DialogActions>
          <Tooltip title=''>
            <IconButton
              onClick={() => {
                setCharacterDetail(0);
                setItemDetail(0);
              }}
            >
              <CloseIcon color='inherit' />
            </IconButton>
          </Tooltip>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default UserInfo;